body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-poppins: Poppins;
  --font-roboto: Roboto;

  /* font sizes */
  --font-size-sm: 14px;
  --font-size-xs: 12px;

  /* Colors */
  --color-gray-100: #fafafb;
  --color-gray-200: #292932;
  --color-gray-300: #1c1c24;
  --color-tomato: #fc5a5a;
  --color-royalblue: #1e75ff;
  --color-darkslategray: #44444f;
  --color-silver: #b5b5be;
  --color-white: #fff;
  --color-lightslategray: #92929d;
  --color-gainsboro: #e2e2ea;

  /* border radiuses */
  --br-xs: 12px;
  --br-81xl: 100px;
  --br-3xs: 10px;
  --br-mini: 15px;
  --br-xl: 20px;
}
